import PropTypes from "prop-types";
import React, { Fragment } from "react";
import MetaTags from "react-meta-tags";
import { BreadcrumbsItem } from "react-breadcrumbs-dynamic";
import Card from "react-bootstrap/Card";
import Accordion from "react-bootstrap/Accordion";
import LayoutThree from "../../layouts/LayoutThree";
import Breadcrumb from "../../wrappers/breadcrumb/Breadcrumb";
import { useFormik } from "formik";
import { Formik } from "formik";
import * as Yup from "yup";

const MyAccount = ({ location }) => {
  const { pathname } = location;
  const initialValues = {
    Fname: "",
    lname: "",
    email: "",
    phone: "",
    fax: "",
  };
  const validationSchema = Yup.object().shape({
    fname: Yup.string()
      .min(2, "Mininum 2 characters")
      .max(15, "Maximum 15 characters")
      .required("Required!"),
   
    lname: Yup.string()
      .min(2, "Mininum 2 characters")
      .max(15, "Maximum 15 characters")
      .required("Required!"),

    email: Yup.string().
    email("Invalid email format")
    .required("Required!"),
    
    phone: Yup.string()
      .min(10, "Mobile be 10 character length")
      .required("Mobile is required!"),

    // phone: Yup.string()
    //   .required("Required!"),
    fax: Yup.string().required("Required!"),
  });
  const handleFormSubmit = (values) => {
    console.log(values);
  };


  const initialValues1 = {
    password: "",
    confirm_password: "",
  };
  const validationSchema1 = Yup.object().shape({
    password: Yup.string()
        .min(6, "Minimum 6 characters")
        .required("password is a required field!"),

      confirm_password: Yup.string()
        .oneOf([Yup.ref("password")], "Password's not match")
        .required("confirm_password is a required field!"),
  });
  const handleFormSubmit1 = (values) => {
    console.log(values);
  };

  return (
    <Fragment>
      <MetaTags>
        <title>Impetrosys</title>
        <meta
          name="description"
          content="Compare page of flone react minimalist eCommerce template."
        />
      </MetaTags>
      {/* <BreadcrumbsItem to={process.env.PUBLIC_URL + "/"}>Home</BreadcrumbsItem>
      <BreadcrumbsItem to={process.env.PUBLIC_URL + pathname}>
        My Account
      </BreadcrumbsItem> */}
      <LayoutThree headerTop="visible">
        {/* breadcrumb */}
        {/* <Breadcrumb /> */}
        {/* <div class="contact-area-new">
          <div class="container">
            <div class="row">
              <div class="col-lg-12">
          <div class="custom-new">
            <h4>MY ACCOUNT</h4>
            </div>
            </div>
            </div>
            </div>
            </div>  */}
        <div className="myaccount-area pb-20 pt-20">
          <div className="container">
            <div className="row">
              <div className="ml-auto mr-auto col-lg-9">
                <h3 className="cart-page-title">My Account</h3>
                <div className="myaccount-wrapper">
                  <Accordion defaultActiveKey="0">
                    <Card className="single-my-account mb-20">
                      <Card.Header className="panel-heading">
                        <Accordion.Toggle variant="link" eventKey="0">
                          <h3 className="panel-title">
                            <span>1 .</span> Edit your account information{" "}
                          </h3>
                        </Accordion.Toggle>
                      </Card.Header>
                      <Accordion.Collapse eventKey="0">
                        <Card.Body>
                          <Formik
                            onSubmit={handleFormSubmit}
                            initialValues={initialValues}
                            validationSchema={validationSchema}
                          >
                            {({
                              values,
                              errors,
                              touched,
                              handleChange,
                              handleBlur,
                              handleSubmit,
                            }) => (
                              <form onSubmit={handleSubmit}>
                                <div className="myaccount-info-wrapper">
                                  <div className="account-info-wrapper">
                                    <h4>My Account Information</h4>
                                    <h5>Your Personal Details</h5>
                                  </div>
                                  <div className="row">
                                    <div className="col-lg-6 col-md-6">
                                      <div className="billing-info">
                                        <label>First Name</label>
                                        <input
                                          type="text"
                                          name="Fname"
                                          value={values.Fname}
                                          onChange={handleChange}
                                          onBlur={handleBlur}
                                        />
                                        {errors.Fname && touched.Fname && (
                                          <p className="text-danger">
                                            {errors.Fname}
                                          </p>
                                        )}
                                      </div>
                                    </div>
                                    <div className="col-lg-6 col-md-6">
                                      <div className="billing-info">
                                        <label>Last Name</label>
                                        <input
                                          type="text"
                                          name="lname"
                                          value={values.lname}
                                          onChange={handleChange}
                                          onBlur={handleBlur}
                                        />
                                        {errors.lname && touched.lname && (
                                          <p className="text-danger">
                                            {errors.lname}
                                          </p>
                                        )}
                                      </div>
                                    </div>
                                    <div className="col-lg-12 col-md-12">
                                      <div className="billing-info">
                                        <label>Email Address</label>
                                        <input
                                          type="email"
                                          name="email"
                                          value={values.email}
                                          onChange={handleChange}
                                          onBlur={handleBlur}
                                        />
                                        {errors.email && touched.email && (
                                          <p className="text-danger">
                                            {errors.email}
                                          </p>
                                        )}
                                      </div>
                                    </div>
                                    <div className="col-lg-6 col-md-6">
                                      <div className="billing-info">
                                        <label>Telephone</label>
                                        <input
                                          name="phone"
                                          type="number"
                                          value={values.phone}
                                          onChange={handleChange}
                                          onBlur={handleBlur}
                                        />
                                        {errors.phone && touched.phone && (
                                          <p className="text-danger">
                                            {errors.phone}
                                          </p>
                                        )}
                                      </div>
                                    </div>
                                    <div className="col-lg-6 col-md-6">
                                      <div className="billing-info">
                                        <label>Fax</label>
                                        <input
                                          name="fax"
                                          type="number"
                                          value={values.fax}
                                          onChange={handleChange}
                                          onBlur={handleBlur}
                                        />
                                        {errors.fax && touched.fax && (
                                          <p className="text-danger">
                                            {errors.fax}
                                          </p>
                                        )}
                                      </div>
                                    </div>
                                  </div>
                                  <div className="billing-back-btn">
                                    <div className="billing-btn">
                                      <button type="submit">Continue</button>
                                    </div>
                                  </div>
                                </div>
                              </form>
                            )}
                          </Formik>
                        </Card.Body>
                      </Accordion.Collapse>
                    </Card>
                    <Card className="single-my-account mb-20">
                      <Card.Header className="panel-heading">
                        <Accordion.Toggle variant="link" eventKey="1">
                          <h3 className="panel-title">
                            <span>2 .</span> Change your password
                          </h3>
                        </Accordion.Toggle>
                      </Card.Header>
                      <Accordion.Collapse eventKey="1">
                        <Card.Body>
                        <Formik
                            onSubmit={handleFormSubmit1}
                            initialValues={initialValues1}
                            validationSchema={validationSchema1}
                          >
                            {({
                              values,
                              errors,
                              touched,
                              handleChange,
                              handleBlur,
                              handleSubmit,
                            }) => (
                          <form onSubmit={handleSubmit}>
                            <div className="myaccount-info-wrapper">
                              <div className="account-info-wrapper">
                                <h4>Change Password</h4>
                                <h5>Your Password</h5>
                              </div>
                              <div className="row">
                                <div className="col-lg-12 col-md-12">
                                  <div className="billing-info">
                                    <label>Password</label>
                                    <input
                                      name="password"
                                      type="password"
                                      value={values.password}
                                      onChange={handleChange}
                                      onBlur={handleBlur}
                                    />
                                    {errors.password && touched.password && (
                                          <p className="text-danger">
                                            {errors.password}
                                          </p>
                                        )}
                                  </div>
                                </div>
                                <div className="col-lg-12 col-md-12">
                                  <div className="billing-info">
                                    <label>Password Confirm</label>
                                    <input
                                      name="confirm_password"
                                      // placeholder=" Confifm Password"
                                      type="Password"
                                      value={values.confirm_password}
                                      onChange={handleChange}
                                      onBlur={handleBlur}
                                    />
                                     {errors.confirm_password && touched.confirm_password && (
                                          <p className="text-danger">
                                            {errors.confirm_password}
                                          </p>
                                        )}

                                  </div>
                                </div>
                              </div>
                              <div className="billing-back-btn">
                                <div className="billing-btn">
                                  <button type="submit">Continue</button>
                                </div>
                              </div>
                            </div>
                          </form>
                          )}
                          </Formik>
                        </Card.Body>
                      </Accordion.Collapse>
                    </Card>
                    <Card className="single-my-account mb-20">
                      <Card.Header className="panel-heading">
                        <Accordion.Toggle variant="link" eventKey="2">
                          <h3 className="panel-title">
                            <span>3 .</span> Modify your address book entries{" "}
                          </h3>
                        </Accordion.Toggle>
                      </Card.Header>
                      <Accordion.Collapse eventKey="2">
                        <Card.Body>
                          <div className="myaccount-info-wrapper">
                            <div className="account-info-wrapper">
                              <h4>Address Book Entries</h4>
                            </div>
                            <div className="entries-wrapper">
                              <div className="row">
                                <div className="col-lg-6 col-md-6 d-flex align-items-center justify-content-center">
                                  <div className="entries-info text-center">
                                    <p>
                                      India Office- 403-Fourth Floor, Fortune
                                      Ambience Business Space, South Tukoganj,
                                      Indore, Madhya Pradesh 452001
                                    </p>
                                    {/* <p>Farhana hayder (shuvo) </p>
                                    <p>hastech </p>
                                    <p> Road#1 , Block#c </p>
                                    <p> Rampura. </p>
                                    <p>Dhaka </p>
                                    <p>Bangladesh </p> */}
                                  </div>
                                </div>
                                <div className="col-lg-6 col-md-6 d-flex align-items-center justify-content-center">
                                  <div className="entries-edit-delete text-center">
                                    <button className="edit">Edit</button>
                                    <button>Delete</button>
                                  </div>
                                </div>
                              </div>
                            </div>
                            <div className="billing-back-btn">
                              <div className="billing-btn">
                                <button type="submit">Continue</button>
                              </div>
                            </div>
                          </div>
                        </Card.Body>
                      </Accordion.Collapse>
                    </Card>
                  </Accordion>
                </div>
              </div>
            </div>
          </div>
        </div>
      </LayoutThree>
    </Fragment>
  );
};

MyAccount.propTypes = {
  location: PropTypes.object,
};

export default MyAccount;
